.table {
  display: grid;
  border-collapse: collapse;
  width: 100%;
  min-width: 100%;
  margin: 0;
  color: $table-color;
  background: $white;
  border-radius: 10px 10px 0 0;
  overflow: hidden;
}

.table thead,
.table tbody,
.table tr {
  display: contents;
}

.table th,
.table td {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #EEF3FB;

  & > *:not(.dropdown) {
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}

.table {
  > :not(caption) > * > * {
    padding: $table-cell-padding-y / 2;
    font-size: $input-font-size;
    font-weight: $font-weight-normal;
    line-height: $input-line-height;
  }

  & .table-head {
    height: 40px;
    background-color: $light;
    border-bottom: none;
    font-weight: $font-weight-bold;
  }

  & .table-head * {
    font-weight: $font-weight-bold;
  }

  & .table-head-check {
    background-color: #eceef3;
  }

  & button,
  & a {
    padding: 0;
    margin: 0;
    color: $table-color;
    background: transparent;
    transition: $transition-base;

    &:hover {
      color: #505C7C;
    }
  }

  & i {
    font-size: 16px;
    transition: $transition-base;
  }

  & td i {
    color: $gray-500;

    &:hover {
      color: $gray-600;
    }
  }

  & .btn-disabled {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: $gray-500 escape-svg($btn-disabled-icon) 50% 0/11px 14px no-repeat;
    transition: $transition-base;

    &:hover {
      background-color: $gray-600;
    }

    &.active {
      background-color: $green;
    }
  }

  & .form-check-input {
    margin-right: 0;
  }

  .table-dropdown {
    position: absolute;

    & .dropdown-toggle {
      display: flex;
      align-items: center;

      & i {
        font-size: 10.5px;
        margin-left: 7px;
      }
    }

    & .dropdown-menu {
      width: 176px;
    }
  }
}

.table-input {
  flex-grow: 1;
  min-width: 65px;
  padding: 0.5rem;
  font-size: $input-font-size;
  font-weight: $font-weight-normal;
  line-height: $input-line-height;
  color: $table-color;
  background: transparent;
  border: 1px solid transparent;
  border-radius: 8px;
  transition: $transition-base;

  &:focus {
    border-color: rgba(158, 168, 194, 0.32);
  }
}

.table-label {
  flex: 1 0 30px;
  padding: 0 8px;
}

.table-remains {
  grid-template-columns: 
    minmax(80px, 3fr) 
    minmax(100px, 4fr) 
    minmax(100px, 4fr)
    minmax(100px, 4fr)
    minmax(100px, 4fr) 
    minmax(100px, 4fr)
    minmax(100px, 4fr);

    & > :not(caption) > * > *:first-child {
      padding-left: 2rem;
    }
}

.buffer {
  position: absolute;
  top: -1000px;
  left: -1000px;
  visibility: hidden;
  white-space: nowrap;
}


.table-list {
  grid-template-columns: 
    minmax(40px, 1fr) 
    minmax(64px, 2fr) 
    minmax(50px, 2fr) 
    minmax(50px, 2.5fr) 
    minmax(150px, 6fr) 
    minmax(45px, 2.5fr) 
    minmax(80px, 3fr) 
    minmax(50px, 3fr) 
    minmax(50px, 3fr) 
    minmax(50px, 3fr) 
    minmax(50px, 3fr) 
    minmax(32px, 1.5fr) 
    minmax(56px, 1.5fr);
}

.table-list {

  & th:first-child {
    padding-left: $table-cell-padding-x / 2;
  }
  
  & > :not(caption) > * > *:last-child {
    padding-right: 1.5rem;
    text-align: right;
  }
}

.table th.disabled,
.table td.disabled {
  & *:not(i) {
    color: #A6AABA;
  }

  & .card-img {
    opacity: 0.35;
  }
}

.table th.head-all-check {
  display: none;
}

.table.table-all-check {

  // & .table-head {

  // }

  & th.head-all-check {
    display: flex;
  }

  & th.head-no-all-check {
    display: none;
  }
}

.colspan-first {
  grid-column: 2 / 12;
}

.colspan-last {
  grid-column: 12 / 14;
}

