.sidebar {
    height: 100%;
    min-height: 100vh;
    flex-shrink: 0;
    background-color: $sidebar-bg;
    border-right: 1px solid rgba(223, 229, 241, 0.5);

    &_top {
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        z-index: 1000;
    }
}

.sidebar-nav {
    display: flex;
    flex-direction: column;
    height: inherit;
    min-height: inherit;
    padding: $sidebar-nav-padding-y $sidebar-nav-padding-x;

    .btn-open {
        margin-top: auto;
    }
}

.sidebar .btn-open {
    display: flex;
    align-items: center;
    padding: 1rem .5rem;
    font-size: $input-font-size;
    font-weight: $input-font-weight;
    line-height: $input-line-height;
    letter-spacing: $input-letter-spacing;
    color: $breadcrumb-color;
    background: transparent;
    transition: color .2s ease-in-out;

    &:hover {
        color: $breadcrumb-color_hover;
    }

    & i {
        padding-right: 10px;
    }
}

.sidebar-link {

    & i {
        position: relative;
        padding: 0 1.5rem 0 .5rem;
    }

    & i::after {
    content: '';
        display: block;
        width: 2rem;
        height: 2rem;
        border-radius: .5rem;
        background: $gray-500;
        opacity: 0.08;
        position: absolute;
        top: -.25rem;
        left: 0;
        z-index: -1;
        transition: $nav-link-transition;
    }
}

.active .sidebar-link {
  
    & i::after {
      background: rgba(255, 255, 255, 0.08);
      opacity: 1;
    }
}