//
// Base styles
//

.alert {
  position: relative;
  display: flex;
  align-items: center;
  padding: $alert-padding-y $alert-padding-x;
  margin-bottom: $alert-margin-bottom;
  border: $alert-border-width solid transparent;
  @include border-radius($alert-border-radius);
  font-size: $alert-font-size;

  &-danger {
    background: $alert-bg_danger;
    color: $alert-color_danger;
    border-color: $alert-border-color_danger;

    &::before {
      content: '';
      display: inline-block;
      vertical-align: baseline;
      margin-right: $alert-icon-margin-right;
      width: 32px;
      height: 32px;
      background: escape-svg($alert-bg-icon_danger) center/cover no-repeat;
    }

    & .close:hover svg * {
      fill: $alert-color_danger_hv;
    }
  }

  &-primary {
    background: $alert-bg_primary;
    color: $alert-color_primary;
    border-color: $alert-border-color_primary;

    &::before {
      content: '';
      display: inline-block;
      vertical-align: baseline;
      margin-right: $alert-icon-margin-right;
      width: 32px;
      height: 32px;
      background: escape-svg($alert-bg-icon_primary) center/cover no-repeat;
    }

    & .close:hover svg * {
      fill: $alert-color_primary_hv;
    }
  }

  &-success {
    background: $alert-bg_success;
    color: $alert-color_success;
    border-color: $alert-border-color_success;

    &::before {
      content: '';
      display: inline-block;
      vertical-align: baseline;
      margin-right: $alert-icon-margin-right;
      width: 32px;
      height: 32px;
      background: escape-svg($alert-bg-icon_success) center/cover no-repeat;
    }

    & .close:hover svg * {
      fill: $alert-color_success_hv;
    }
  }

  & strong {
    padding-right: .25rem;
  }
}

// Headings for larger alerts
.alert-heading {
  // Specified to prevent conflicts of changing $headings-color
  color: inherit;
}

// Provide class for links that match alerts
.alert-link {
  font-weight: $alert-link-font-weight;
}


// Dismissible alerts
//
// Expand the right padding and account for the close button's positioning.

.alert-dismissible {
  padding-right: $alert-dismissible-padding-r;

  // Adjust close link position
  .close {
    position: absolute;
    top: 4px;
    right: 4px;
    z-index: $stretched-link-z-index + 1;
    padding: $alert-padding-y $alert-padding-x * 2;
    background: transparent;
  }
}


