.wrapper {
    display: flex;
}

.title_margin-bottom {
    margin-bottom: 1rem;
}

.form-group {
    position: relative;
    margin-bottom: 1rem;
}

.form-search {
    margin-bottom: 0;
}

.content {
    flex-grow: 1;
    background: $white;
    box-shadow: $main-box-shadow;
    border-radius: .5rem;
    overflow-x: auto;
}

i.icon-light {
    color: $icon-light;
}

.main {
    display: flex;
    flex-direction: column;
    padding-bottom: 2rem;
    position: relative;
    z-index: 1000;
    background-color: $main-bg;
    box-shadow: $main-box-shadow;
}

.main-head {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: $spacer;
}

.main-content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.footer-list {
    display: flex;
    align-items: center;
    padding-top: 1rem;
    margin-top: auto;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.01em;
    color: #9EA8C2;
}

.footer-select-box {
    display: flex;
    align-items: center;
    margin: 0 2rem;

    & label {
        margin-right: 8px;
    }
}

.footer-count-box {
    margin: 0 0 0 auto;
}

.gallery__content {
    margin-bottom: 40px;
}

.gallery__img,
.gallery__video {
    margin-top: 1.5rem;
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: 1fr;
}

.gallery__card {
    position: relative;
    height: 11.5vw;
    width: 100%;
    border-radius: 0.5rem;
    overflow: hidden;
    background: #EEF3FB;
}

.gallery__card img,
.gallery__card video {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 3;
}

.gallery__progress {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    text-align: center;

    & .progress {
        width: 50%;
        margin: 0 auto;
    }
}

.gallery__progress-info {
    display: block;
    margin-top: 12px;
    padding: 0 16px;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.01em;
    color: #9EA8C2;
}

.card-img {
    width: 48px;
    height: 48px;
    background: #E3E7F1 escape-svg($img-default) center/16px 14px no-repeat;
}

.card-select {
    position: relative;
}

.connections-title {
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: $gray-600;
}

.row-form {
    width: 690px;
    max-width: 690px;
}

.col-form {
    width: 660px;
    max-width: 660px;
}

@media (min-width: 1920px) {

    .gallery__img,
    .gallery__video {
        grid-template-columns: repeat(8, 1fr);
    }

    .gallery__card {
        height: 9vw;
    }
}