//
// Base styles
//

.btn {
  display: inline-block;
  width: 128px;
  font-family: $btn-font-family;
  font-weight: $btn-font-weight;
  line-height: $btn-line-height;
  color: $btn-color;
  text-align: center;
  white-space: $btn-white-space;
  vertical-align: middle;
  cursor: if($enable-button-pointers, pointer, null);
  user-select: none;
  background: $btn-bg;
  @include button-size($btn-padding-y, $btn-padding-x, $btn-font-size, $btn-border-radius);
  @include transition($btn-transition);

  &_big {
    width: 176px;
  }

  &_add {
    position: relative;
    width: 176px;

    &::after {
      content: '';
      display: inline-block;
      vertical-align: baseline;
      position: relative;
      top: 4px;
      left: 12px;
      width: 16px;
      height: 16px;
      background: escape-svg($btn-add-icon) center/16px no-repeat;
    }

    &:disabled,
    &.disabled {
      
      &::after {
        background: escape-svg($btn-add-icon_disabled) center/16px no-repeat;
      }
    }
  }

  &:hover {
    background: $btn-bg_hv;
  }

  .btn-check:checked + &,
  .btn-check:active + &,
  &:active,
  &.active {
    @include box-shadow($btn-active-box-shadow);

    &:focus {
      @include box-shadow($btn-focus-box-shadow, $btn-active-box-shadow);
    }
  }

  &:disabled,
  &.disabled,
  fieldset:disabled & {
    pointer-events: none;
    color: $btn-color_ds;
    background: $btn-bg_ds;
  }
}


//
// Alternate buttons
//

@each $color, $value in $theme-colors {
  .btn-#{$color} {
    @include button-variant($value, $value);
  }
}

.btn-search {
  position: absolute;
  top: 6px;
  right: 6px;
  width: 26px;
  height: 26px;
  background-color: transparent;

  & i {
    color: #9EA8C2;
    transition: $transition-base;
  }

  &:hover i {
    color: #6A7797;
  }
}

//
// Link buttons
//

// Make a button look and behave like a link
.link {
  font-weight: $font-weight-normal;
  color: $btn-link-color;
  text-decoration: $link-decoration;
  @include transition($btn-transition);

  &:hover {
    color: $btn-link-hover-color;
    text-decoration: $link-hover-decoration;
  }

  &:focus {
    text-decoration: $link-hover-decoration;
  }

  &:disabled,
  &.disabled {
    color: $btn-link-disabled-color;
  }

  // No need for an active state here
}


.btn-light {
  font-size: $btn-font-size;
  line-height: $btn-line-height;
  letter-spacing: $main-letter-spacing;
  color: $gray-600;
  background: transparent;
  margin-left: auto;
  margin-right: auto;
  @include transition($btn-transition);

  &:hover {
    color: #505C7C;
  }
}

.btn-lighter {
  font-size: $btn-font-size;
  line-height: $btn-line-height;
  letter-spacing: $heading-letter-spacing;
  color: $gray-500;
  background: transparent;
  padding: $btn-padding-y 0.5rem;
  @include transition($btn-transition);

  &:hover {
    color: $gray-600;
  }
}

.btn-sort-price {
  display: flex;

  & .btn-sort-arrows {
    display: flex;
    flex-direction: column;
    margin-left: 12px;

    & i {
      font-size: 10.5px;
      max-height: 7px;
      opacity: 0.3;
      transition: $transition-base;

      &:first-child {
        opacity: 1;
      }
    }
  }

  &.with-more .btn-sort-arrows i {

    &:first-child {
      opacity: 0.3;
    }

    &:last-child {
      opacity: 1;
    }
  }
}

.btn_file {
  width: auto;
  padding: 0.58rem 1.5rem;

  &::after {
    left: 7px;
  }
}


//
// Button Sizes
//

.btn-lg {
  @include button-size($btn-padding-y-lg, $btn-padding-x-lg, $btn-font-size-lg, $btn-border-radius-lg);
}

.btn-sm {
  @include button-size($btn-padding-y-sm, $btn-padding-x-sm, $btn-font-size-sm, $btn-border-radius-sm);
}
