// Base class
//
// Kickstart any navigation component with a set of style resets. Works with
// `<nav>`s, `<ul>`s or `<ol>`s.

.nav {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.nav-item {
  position: relative;
  background-color: #ccc;
  background: transparent;
  backface-visibility: hidden;
  z-index: 1;
  @include border-radius($nav-item-border-radius, 0);
  transition: $transition-base;

  &.active,
  &.active:hover {
    background: $nav-item-active-bg;
  }

  &:hover {
    background: $gray-300;
  }
}

.nav-item:not(:last-child) {
  margin-bottom: .5rem;
}

.nav-link {
  display: flex;
  position: relative;
  z-index: 3;
  padding: 0.75rem $nav-link-padding-x 0.7rem;
  @include font-size($nav-link-font-size);
  font-weight: $nav-link-font-weight;
  color: $nav-link-color;
  background: transparent;
  text-decoration: if($link-decoration == none, null, none);
  @include transition($nav-link-transition);

  &:hover,
  &:focus {
    color: $nav-link-color;
  }

  &.disabled {
    color: $nav-link-disabled-color;
    pointer-events: none;
    cursor: default;
  }
}

.active .nav-link,
.active .nav-link:hover {
  color: $nav-link-active-color;
  text-decoration: if($link-hover-decoration == underline, none, null);
}

//
// Tabs
//

.nav-tabs {
  border-bottom: $nav-tabs-border-width solid $nav-tabs-border-color;

  .nav-link {
    margin-bottom: -$nav-tabs-border-width;
    border: $nav-tabs-border-width solid transparent;
    @include border-top-radius($nav-tabs-border-radius);

    &:hover,
    &:focus {
      border-color: $nav-tabs-link-hover-border-color;
    }

    &.disabled {
      color: $nav-link-disabled-color;
      background-color: transparent;
      border-color: transparent;
    }
  }

  .nav-link.active,
  .nav-item.show .nav-link {
    color: $nav-tabs-link-active-color;
    background-color: $nav-tabs-link-active-bg;
    border-color: $nav-tabs-link-active-border-color;
  }

  .dropdown-menu {
    // Make dropdown border overlap tab border
    margin-top: -$nav-tabs-border-width;
    // Remove the top rounded corners here since there is a hard edge above the menu
    @include border-top-radius(0);
  }
}


//
// Pills
//

.nav-pills {
  padding: 0 2rem;
  border-bottom: 1px solid #eeeff4;

  .nav-item-pills:not(:last-child) {
    margin-right: 40px;
  }

  .dropdown-toggle i {
    font-size: 12px;
    padding-left: 6px;
    color: $nav-pills-link-color;
  }

  // .dropdown-toggle.show i {
  //   color: $nav-pills-link-active-color;
  // }

  .nav-link,
  .nav-link:hover {
    padding: $nav-link-padding-y * 1.7 0;
    font-weight: 500;
    font-size: $nav-pills-font-size;
    line-height: $nav-pills-line-height;
    color: $nav-pills-link-color;
    // @include border-radius($nav-pills-border-radius);
  }

  .nav-link.active,
  .nav-link.show {
    color: $nav-pills-link-active-color;

    &:after {
      content: '';
      position: absolute;
      bottom: -1px;
      left: 0;
      width: 100%;
      height: 2px;
      background: $nav-pills-link-active-color;
    }
  }
}

.nav-pills .dropdown-box .nav-link {
  padding-right: 18px;

  // &.active {
  //   display: none;
  // }
}


//
// Justified variants
//

.nav-fill {
  > .nav-link,
  .nav-item {
    flex: 1 1 auto;
    text-align: center;
  }
}

.nav-justified {
  > .nav-link,
  .nav-item {
    flex-basis: 0;
    flex-grow: 1;
    text-align: center;
  }
}


// Tabbable tabs
//
// Hide tabbable panes to start, show them when `.active`

.tab-content {
  > .tab-pane {
    display: none;
  }
  > .active {
    display: block;
  }
}

.tab-pane {
  padding: 2rem;
}
